*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins',sans-serif;
}
.calculator.dark-body{
    background-color: #17181a;
}
.calculator{
    height: 80vh;
    width: 100%;
    
    display: flex;
    align-items: center;
    justify-content: center;
}

.content{
    background: #17181a;
    padding: 26px;
    border-radius: 20px;
    box-shadow: 0px 80px 50px -30px rgba(41, 168, 255, 0.2);
}

input[type="text"]{
    width: 330px;
    padding: 25px;
    margin: 10px;
    background: transparent;
    border: none;
    outline: none;
    font-size: 45px;
    text-align: right;
    color: #fff;
}

input[type="text"]::placeholder{
    color: #fff;
}

input[type="button"]{
    border: none;
    outline: none;
    width: 60px;
    height: 60px;
    border-radius: 10px;
    background: #303136;
    font-size: 21px;
    font-weight: 600;
    color: #29a8ff;
    margin: 10px;
    cursor: pointer;
}


 


